.App {
  font-family: Arial, sans-serif;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

h1 {
  color: #333;
}

.session-management {
  margin: 20px 0;
}

.join-session {
  margin-top: 10px;
}

.join-session input {
  padding: 5px;
  margin-right: 10px;
  width: 200px;
}

.active-session {
  background-color: #f8f9fa;
  padding: 15px;
  border-radius: 4px;
}

.share-options {
  margin-top: 10px;
}

.share-options p {
  margin: 5px 0;
  word-break: break-all;
}

.qr-code {
  margin-top: 10px;
}

.qr-code img {
  max-width: 200px;
  margin-top: 10px;
}

button {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin: 5px;
}

button:hover {
  background-color: #0056b3;
}

.status {
  margin-top: 10px;
  color: #666;
}

.file-management {
  margin-top: 30px;
  text-align: left;
}

.upload-section {
  margin-bottom: 20px;
}

.upload-section input {
  margin-right: 10px;
}

.file-list {
  list-style-type: none;
  padding: 0;
}

.file-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.download-btn {
  background-color: #28a745;
}

.download-btn:hover {
  background-color: #218838;
}

.delete-btn {
  background-color: #dc3545;
}

.delete-btn:hover {
  background-color: #c82333;
}

.progress-bar {
  width: 100%;
  background-color: #ddd;
  border-radius: 4px;
  margin-top: 10px;
}

.progress-bar div {
  height: 20px;
  background-color: #4caf50; 
  text-align: center;
  color: white;
  border-radius: 4px;
  transition: width 0.3s ease-in-out;
}